* {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}


.state {
  max-width: 640px;
  width: calc(100% -20px);
  margin: 0 auto;
  border: 2px solid #333;
  padding: 30px;
  background-color: wheat;
}
.state h1 {
  margin-bottom: 20px;
}
.state input {
  width: 160px;
  height: 30px;
  margin-right: 20px;
}
.state button {
  width: 40px;
  height: 30px;
  background-color: slategray;
  color: wheat;
}
.state img {
  width: 240px;
  height: 240px;
  object-fit: cover;
  display: block;
  margin-bottom: 10px;
}
.state p {
  margin-top: 10px;
}
.state p span {
  font-size: 14px;
  color: slategray;
}
